import React, {useEffect} from 'react';
import {Container} from "react-bootstrap";
import image from '../../../../assets/images/Waiting.png'

const Section06 = ({onNext}) => {
    useEffect(() => {
        const timer = setTimeout(() => {
            onNext();  // 1.5초 후에 다음 섹션으로 이동
        }, 1500);

        // 컴포넌트가 unmount 될 때 타이머 정리
        return () => clearTimeout(timer);
    }, [onNext]);
    return (
        <Container>
            <div className="my-5">
                <img src={image} width={350} className="m-auto d-block w-mo-100" alt=""/>
            </div>
        </Container>
    );
};

export default Section06;
