import React, {useState} from 'react';
import {Tab, Table, Tabs} from "react-bootstrap";

const ExchangeTab = ({tabData, data}) => {
    const [tabKey, setTabKey] = useState('All')
    return (
        <div className="mobile-exchange-tab">
            <Tabs
                activeKey={tabKey}  // 부모 컴포넌트에서 관리되는 현재 활성 탭
                onSelect={(k) => setTabKey(k)}  // 탭이 변경될 때마다 상태를 업데이트
                id="justify-tab-example"
                className="mb-3 fs-6 overflow-hidden gap-2 "
                justify
            >

                {
                    tabData.map((item) => (
                        <Tab eventKey={item.key} title={item.title} key={item.key}>  {/* key가 필요함 */}
                            <Table className="mb-5">
                                <colgroup>
                                    <col/>
                                    {/*<col width="22%"/>*/}
                                </colgroup>
                                <tbody>
                                {data.filter(exchange => {
                                    if (tabKey === "All") return true;
                                    if (tabKey === "Recommendation" && exchange.score > 9.5) return true;
                                    if (tabKey === "Event" && parseInt(exchange.avgRefund.replace(/[^0-9]/g, '')) >= 1000000) {
                                        return true;
                                    }
                                    if (tabKey === "Payback" && exchange.cashbackRate === "70%") return true;
                                    if (tabKey === "Designation" && exchange.limitPrice > 0.01) return true;
                                    if (tabKey === "Market" && exchange.marketPrice > 0.02) return true;
                                    return false;
                                }).map(exchange => (
                                    <tr key={exchange.exchangeName} className="align-middle ">
                                        <td className="text-start">
                                            <div className="d-flex align-items-center gap-2">
                                                <img src={exchange.icon} alt="" width={30}/>
                                                <div className="d-flex flex-column justify-content-between">
                                                    <strong>{exchange.exchangeName}</strong>
                                                    <div
                                                        className="d-flex align-items-center gap-1">{exchange.description}
                                                        <img src={exchange.descriptionIcon} width={16} alt=""/></div>
                                                </div>
                                            </div>
                                        </td>
                                        <td className="text-end"><span className={`
                                    px-2 text-black p-1 rounded-4 fs-7 fw-bold
                                    ${["Bybit", "DeepCoin", "Bitget"].includes(exchange.exchangeName) ? "bg-warning" : "bg-orange"}`}>{exchange.mobilePaybackRate}</span>
                                        </td>
                                    </tr>
                                ))}


                                </tbody>
                            </Table>
                        </Tab>
                    ))}
            </Tabs>
        </div>
    );
};

export default ExchangeTab;
