import React, {useState} from 'react';
import {Button, Container} from "react-bootstrap";
import image from "../../../../assets/images/section08banner.png"

const Section08 = ({onPrevious, onNext}) => {
    const [currentSection, setCurrentSection] = useState(8); // 현재 섹션을 관리
    const handlePrevious = () => {
        if (currentSection > 1) {
            setCurrentSection(currentSection - 1); // 이전 섹션으로 이동
            onPrevious(); // 부모 컴포넌트에서 "이전" 기능을 처리할 수도 있습니다
        }
    };
    return (
        <Container className="my-5 py-5">
            <div className="text-center mt-4">
                <h2>
                    <span className="color-primary">페이백</span>을 받지 않는다면 계속 손실돼요
                </h2>
                <p className="text-muted">손실없이 환급액 계속 챙겨 봐요</p>
            </div>
            <div>
                <img src={image} width={500} className="m-auto d-block w-mo-100" alt=""/>
            </div>
            <div>
                <p className="text-muted text-center">
                    *본 금액은 고객님이 입력하신 거래 데이터를 기반으로 한<br/>Bybit 거래소의 일반적인 커미션 금액입니다.
                </p>
            </div>
            <div className="paybackbtncustop d-flex justify-content-center gap-3 mt-4 mb-5 pt-5">
                <Button
                    type="button"
                    className="w-100 rounded-4 py-3 fw-bold"
                    onClick={handlePrevious} // 이전 버튼 클릭 시 "이전" 섹션으로 이동
                >
                    이전
                </Button>
                <Button
                    type="button"
                    className="w-100 rounded-4 fw-bold"
                    onClick={onNext}
                >
                    다음
                </Button>
            </div>
        </Container>
    );
};

export default Section08;
