import React, {useState} from 'react';
import {Container, InputGroup, Form, Modal, Row, Col} from "react-bootstrap";
import searchIcon from "../../../assets/images/icon/search.png";
import bottomArrowIcon from "../../../assets/images/icon/bottonarrow.png";
import Union from "../../../assets/images/icon/Union.png";

import {Swiper, SwiperSlide} from 'swiper/react';
import {Autoplay} from "swiper/modules";
import SwiperCore from "swiper";

const refundAmount = [
    {name: "Bybit", amount: "69만원"},
    {name: "BloFin", amount: "81만원"},
    {name: "MEXC", amount: "76만원"},
    {name: "Bitget", amount: "151만원"},
    {name: "OKX", amount: "88만원"},
    {name: "BitMex", amount: "4만원"},
    {name: "GateIo", amount: "25만원"},
    {name: "BingX", amount: "20만원"},
    {name: "Bybit", amount: "69만원"},
    {name: "BloFin", amount: "81만원"},
    {name: "MEXC", amount: "76만원"},
    {name: "Bitget", amount: "151만원"},
    {name: "OKX", amount: "88만원"},
    {name: "BitMex", amount: "4만원"},
    {name: "GateIo", amount: "25만원"},
    {name: "BingX", amount: "20만원"},

];

const icons = [
    {icon: require("../../../assets/images/icon/Bybit.png"), name: "Bybit"},
    {icon: require("../../../assets/images/icon/Bitget.png"), name: "Bitget"},
    {icon: require("../../../assets/images/icon/okx.png"), name: "OKX"},
    {icon: require("../../../assets/images/icon/MEXC.png"), name: "MEXC"},
    {icon: require("../../../assets/images/icon/bingx.png"), name: "BingX"},
    {icon: require("../../../assets/images/icon/gateio.png"), name: "GateIo"},
    {icon: require("../../../assets/images/icon/Blofin.png"), name: "Blofin"},
    {icon: require("../../../assets/images/icon/Deepcoin.png"), name: "Deepcoin"},
    {icon: require("../../../assets/images/icon/coincatch.png"), name: "CoinCatch"},
];

const UIDsearch = () => {
    const [show, setShow] = useState(false);
    const [selectedIcon, setSelectedIcon] = useState(icons[0]); // 기본 아이콘 설정

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    // 아이콘을 클릭했을 때 selectedIcon 업데이트
    const handleIconClick = (item) => {
        setSelectedIcon(item);
        setShow(false);  // 아이콘 선택 후 모달 닫기
    };
    SwiperCore.use([Autoplay]);


    return (
        <div className="uidSearch">
            <Container>
                <div className="bg-primary text-white rounded-4 p-4 overflow-hidden soft-shadow mb-4">
                    <h6 className="text-end">선물거래 수수료 페이백 플랫폼, 테더베이스 <span className="fs-2">PayBack!</span></h6>
                    <h6>클릭 한번으로 <span className="text-black">페이백</span> 받기</h6>
                    <div
                        className="bg-white text-black p-2 d-flex justify-content-between align-items-center w-100 gap-2">
                        <button
                            className="border-0 bg-white d-flex justify-content-center align-items-center gap-2"
                            onClick={handleShow}
                        >
                            <img src={selectedIcon.icon} width={30} height={30} alt=""/>
                            {selectedIcon.name}
                            <img src={bottomArrowIcon} width={10} alt=""/>
                        </button>
                        <InputGroup className="w-100">
                            <Form.Control
                                placeholder="UID를 입력해주세요."
                                aria-label="Username"
                                aria-describedby="basic-addon1"
                                className="border-0 border-bottom rounded-0"
                            />
                        </InputGroup>
                        <button className="border-0 bg-white">
                            <img src={searchIcon} width={20} height={20} alt=""/>
                        </button>
                    </div>
                    <hr className="border-2 border-white opacity-100"/>
                    <div className="md-5 d-flex align-items-center gap-3 justify-content-between align-items-center flex-wrap">
                        <h6 className="fs-5 mb-0">1인 평균 환급액</h6>
                        <div className="w-sm-100 overflow-hidden" style={{width: "100%"}}>
                            <Swiper
                                className="mySwiper"
                                autoplay={{
                                    delay: 0,  // 슬라이드가 자동으로 넘어가는 시간 간격
                                    disableOnInteraction: false  // 사용자가 슬라이드를 넘겨도 autoplay 유지
                                }}
                                slidesPerView={'auto'}  // 한 번에 2개의 슬라이드를 표시
                                spaceBetween={10}  // 슬라이드 간 간격 0
                                centeredSlides={true}  // 슬라이드가 중앙에 배치
                                loop={true}  // 슬라이드가 끝나면 처음으로 돌아가도록 설정
                                loopAdditionalSlides={3} // 마지막 슬라이드에서 멈칫하는 현상 방지
                                freeMode={true}  // 자유롭게 슬라이드 이동
                                // observer={true}  // DOM 변경 감지
                                observeParents={true}  // 부모 요소의 변경 감지
                                speed={2000}  // 슬라이드 전환 속도
                                allowTouchMove={false}  // 슬라이드 터치로 이동 방지
                            >
                                {refundAmount.map((item, index) => (
                                    <SwiperSlide className=" border-0 bg-transparent" key={index}>
                                        <div className=" bg-warning p-2 rounded-3 h6 mb-0 text-center">
                                            {item.name} <span className="text-black">{item.amount}</span>
                                        </div>
                                    </SwiperSlide>
                                ))}
                            </Swiper>
                        </div>
                    </div>
                </div>
                {/* 모달 창 */}
                <Modal show={show} onHide={handleClose}>
                    <Modal.Header closeButton className="border-0">
                        <div className="d-flex align-items-center gap-1">
                            <span className="w-2px "></span>
                            <Modal.Title className="h5 fw-bold">거래소 선택</Modal.Title>
                        </div>
                    </Modal.Header>
                    <Modal.Body>
                        <Row className="row-gap-4">
                            {icons.map((item, index) => (
                                <Col lg={6} className="position-relative" key={index}>
                                    <div
                                        className="d-flex align-items-center justify-content-between pointer-event"
                                        onClick={() => handleIconClick(item)}
                                        style={{cursor: "pointer"}}// 아이콘 클릭 시 선택 처리
                                    >
                                        <div className="d-flex align-items-center gap-2">
                                            <img src={item.icon} width={35} height={35} alt=""/>
                                            <span>{item.name}</span>
                                        </div>
                                        {item.name === "Bybit" || item.name === "Bitget" || item.name === "OKX" ? (
                                            <img src={Union} width={20} alt=""/>
                                        ) : null}
                                    </div>
                                </Col>
                            ))}
                        </Row>
                    </Modal.Body>
                </Modal>
            </Container>
        </div>
    );
};

export default UIDsearch;
