import React from "react";
import Header from "./components/Header/Header";
import Footer from "./components/Footer/Footer";
import Main from "./pages/Main/Main";
import {Route, Routes, useLocation} from 'react-router-dom';

import Partnership from "./pages/Partnership/Partnership";
import Service from "./pages/Service/Service";
import BlackHeader from "./components/Header/BlackHeader";
import PaybackTest from "./pages/PaybackTest/PaybackTest";
import Login from "./pages/Login/Login";
import Sign from "./pages/Sign/Sign";

function App() {
    const location = useLocation();
    return (
        <div className="App">
            {
                location.pathname === "/service" ? <BlackHeader/> : <Header/>
            }
            <Routes>
                <Route exact path="/" element={<Main/>} />
                <Route exact path="/partnership" element={<Partnership/>} />
                <Route exact path="/service" element={<Service/>} />
                <Route exact path="/paybacktest" element={<PaybackTest/>} />
                <Route exact path="/login" element={<Login/>} />
                <Route exact path="/sign" element={<Sign/>} />
            </Routes>
            {
                location.pathname === "/service" ? "" : <Footer/>
            }

        </div>
    );
}

export default App;
