import React, { useState } from 'react';
import { Button, Container } from "react-bootstrap";

const icondata = [
    { name: "Bybit", icon: require('../../../../assets/images/icon/Bybit.png') },
    { name: "TABBIT", icon: require('../../../../assets/images/icon/tabit.png') },
    { name: "MEXC", icon: require('../../../../assets/images/icon/MEXC.png') },
    { name: "DeepCoin", icon: require('../../../../assets/images/icon/Deepcoin.png') },
    { name: "OKX", icon: require('../../../../assets/images/icon/okx.png') },
    { name: "BitMart", icon: require('../../../../assets/images/icon/bitmart.png') },
    { name: "BloFin", icon: require('../../../../assets/images/icon/Blofin.png') },
    { name: "CoinCash", icon: require('../../../../assets/images/icon/coincatch.png') },
    { name: "BitGet", icon: require('../../../../assets/images/icon/Bitget.png') },
    { name: "Gate.io", icon: require('../../../../assets/images/icon/gateio.png') },
    { name: "HTX", icon: require('../../../../assets/images/icon/HTX.png') },
];

const Section02 = ({ onNext, onPrevious }) => {
    const [activeIndex, setActiveIndex] = useState(null);
    const [currentSection, setCurrentSection] = useState(2); // 현재 섹션을 관리
    const handlePrevious = () => {
        if (currentSection > 1) {
            setCurrentSection(currentSection - 1); // 이전 섹션으로 이동
            onPrevious(); // 부모 컴포넌트에서 "이전" 기능을 처리할 수도 있습니다
        }
    };

    const handleClick = (index) => {
        // 클릭한 항목의 인덱스를 상태로 설정
        setActiveIndex(index);
    };

    return (
        <Container className="my-5">
            <div className="d-flex justify-content-center gap-3">
                <div className="paybackBar active"></div>
                <div className="paybackBar"></div>
                <div className="paybackBar"></div>
                <div className="paybackBar"></div>
            </div>
            <div className="text-center mt-4">
                <h2><span className="color-primary">가장 자주 쓰는 거래소</span> 하나를 선택해 주세요.</h2>
                <p className="text-muted">어떤 거래소를 이용 중이신가요?</p>
            </div>
            <div className="paybackGrid d-grid mt-3">
                {icondata.map((item, index) => (
                    <div
                        key={index}
                        className={`gridBox d-flex flex-column align-items-center gap-1 border rounded-4 py-3 px-3 ${activeIndex === index ? 'active' : ''}`} // active 클래스 추가/제거
                        onClick={() => handleClick(index)} // 클릭 시 handleClick 호출
                    >
                        <img src={item.icon} width={32} alt={item.name} />
                        <span className="mb-0 fs-7">{item.name}</span>
                    </div>
                ))}
            </div>
            <div className="paybackbtncustop d-flex justify-content-center gap-3 mt-4">
                <Button
                    type="button"
                    className="w-100 rounded-4 py-3 fw-bold"
                    onClick={handlePrevious} // 이전 버튼 클릭 시 "이전" 섹션으로 이동
                >
                    이전
                </Button>
                <Button
                    type="button"
                    className="w-100 rounded-4 fw-bold"
                    onClick={onNext}
                    disabled={activeIndex === null} // activeIndex가 null일 경우 버튼 비활성화
                >
                    다음
                </Button>
            </div>
        </Container>
    );
};

export default Section02;
