import React from 'react';
import {Swiper, SwiperSlide} from "swiper/react";
import {Autoplay, Pagination} from "swiper/modules";
import SwiperCore from "swiper";
import {Container} from "react-bootstrap";

const BannerSwiper = () => {
    SwiperCore.use([Autoplay]);
    return (
        <Container>
            <div className="bannerSwiper position-relative rounded-4 overflow-hidden">
                <Swiper
                    autoplay={{
                        delay: 2000,  // 슬라이드가 자동으로 넘어가는 시간 간격
                        disableOnInteraction: false  // 사용자가 슬라이드를 넘겨도 autoplay 유지
                    }}
                    pagination={{
                        type: 'fraction',
                    }}
                    loop={true}
                    modules={[Pagination]}
                    className="mySwiper"
                >
                    <SwiperSlide style={{height: "300px"}} className="bg-secondary">이벤트 배너영역</SwiperSlide>
                    <SwiperSlide style={{height: "300px"}} className="bg-secondary">이벤트 배너영역</SwiperSlide>
                    <SwiperSlide style={{height: "300px"}} className="bg-secondary">이벤트 배너영역</SwiperSlide>
                </Swiper>
            </div>
        </Container>
    );
};

export default BannerSwiper;
