import React, { useState } from 'react';
import Section01 from "./components/Section01/Section01";
import Section02 from "./components/Section02/Section02";
import Section03 from "./components/Section03/Section03";
import Section04 from "./components/Section04/Section04";
import Section05 from "./components/Section05/Section05";
import Section06 from "./components/Section06/Section06";
import Section07 from "./components/Section07/Section07";
import Section08 from "./components/Section08/Section08";
import Section09 from "./components/Section09/Section09";
import Section10 from "./components/Section10/Section10";

const PaybackTest = () => {
    const [currentSection, setCurrentSection] = useState(1);  // 현재 섹션 상태

    const handleNextSection = () => {
        if (currentSection < 10) {
            setCurrentSection(currentSection + 1);  // 섹션 번호 증가
        } else {
            console.log('모든 섹션을 완료했습니다!');
        }
    };

    const handlePreviousSection = () => {
        if (currentSection > 1) {
            setCurrentSection(currentSection - 1);  // 섹션 번호 감소
        } else {
            console.log('첫 번째 섹션입니다!');
        }
    };

    const sections = [
        <Section01 onNext={handleNextSection} onPrevious={handlePreviousSection} />,
        <Section02 onNext={handleNextSection} onPrevious={handlePreviousSection} />,
        <Section03 onNext={handleNextSection} onPrevious={handlePreviousSection} />,
        <Section04 onNext={handleNextSection} onPrevious={handlePreviousSection} />,
        <Section05 onNext={handleNextSection} onPrevious={handlePreviousSection} />,
        <Section06 onNext={handleNextSection} onPrevious={handlePreviousSection} />,
        <Section07 onNext={handleNextSection} onPrevious={handlePreviousSection} />,
        <Section08 onNext={handleNextSection} onPrevious={handlePreviousSection} />,
        <Section09 onNext={handleNextSection} onPrevious={handlePreviousSection} />,
        <Section10 onNext={handleNextSection} onPrevious={handlePreviousSection} />
    ];

    return (
        <div className="container-fluid">
            {sections[currentSection - 1]}
        </div>
    );
};

export default PaybackTest;
