import React, { useState } from 'react';
import { Button, Container } from "react-bootstrap";

const trade = [
    "하루 1회 미만",
    "하루 1회 ~ 2회 거래",
    "하루 2회 ~ 5회 거래",
    "하루 5회 ~ 10회 거래",
    "하루 10회 이상 거래",
]

const Section05 = ({ onNext, onPrevious }) => {
    const [activeIndex, setActiveIndex] = useState(null); // 선택된 항목의 인덱스를 저장
    const [currentSection, setCurrentSection] = useState(5); // 현재 섹션을 관리
    const handlePrevious = () => {
        if (currentSection > 1) {
            setCurrentSection(currentSection - 1); // 이전 섹션으로 이동
            onPrevious(); // 부모 컴포넌트에서 "이전" 기능을 처리할 수도 있습니다
        }
    };
    const handleClick = (index) => {
        setActiveIndex(index); // 클릭된 항목의 인덱스를 업데이트
    };



    return (
        <Container className="my-5 py-5">
            <div className="d-flex justify-content-center gap-3">
                <div className="paybackBar active"></div>
                <div className="paybackBar active"></div>
                <div className="paybackBar active"></div>
                <div className="paybackBar active"></div>
            </div>
            <div className="text-center mt-4">
                <h2>
                    하루에 몇 번 정도 <span className="color-primary">거래</span>하시나요?
                </h2>
                <p className="text-muted">거래 빈도를 입력해주세요.</p>
            </div>
            <div className="d-flex flex-column gap-4">
                {
                    trade.map((item, index) => (
                        <div
                            key={index}
                            className={`tradeItem w-100 border text-center py-3 rounded-4 color-secondary ${activeIndex === index ? 'active' : ''}`}
                            onClick={() => handleClick(index)} // 클릭 시 handleClick 호출
                        >
                            <h3 className="mb-0">{item}</h3>
                        </div>
                    ))
                }
            </div>
            <div className="paybackbtncustop d-flex justify-content-center gap-3 mt-4 mb-5 pt-5">
                <Button
                    type="button"
                    className="w-100 rounded-4 py-3 fw-bold"
                    onClick={handlePrevious} // 이전 버튼 클릭 시 "이전" 섹션으로 이동
                >
                    이전
                </Button>
                <Button
                    type="button"
                    className="w-100 rounded-4 fw-bold"
                    onClick={onNext}
                    disabled={activeIndex === null} // activeIndex가 null이면 버튼 비활성화
                >
                    다음
                </Button>
            </div>
        </Container>
    );
};

export default Section05;
