import React, {useEffect, useState} from 'react';
import logo from '../../assets/images/blackLogo.png';
import bicLogo from '../../assets/images/blackLogo.png';
import homeIcon from '../../assets/images/icon/homeIcon.png';
import blackClose from '../../assets/images/icon/blackCloseIcon.png';
import arrowRightIcon from '../../assets/images/icon/arrowRightIcon.png';
import { Container} from "react-bootstrap";
import {Link} from "react-router-dom";

const link = [
    {name: "제휴 거래소", href: "/partnership", icon: require("../../assets/images/icon/navIcon04.png")},
    {name: "서비스 소개", href: "/service", icon: require("../../assets/images/icon/navIcon01.png") },
    {name: "페이백 테스트", href: "/paybacktest", icon: require("../../assets/images/icon/navIcon02.png") },
]

const Header = () => {
    const [isActive, setIsActive] = useState(false)
    const handleClick = (e) => setIsActive(!isActive)
    const handleClose = () => {
        setIsActive(false)
    }
    // 스크롤을 비활성화하는 useEffect
    useEffect(() => {
        if (isActive) {
            document.body.style.overflow = 'hidden'; // 스크롤 비활성화
        } else {
            document.body.style.overflow = 'auto'; // 스크롤 활성화
        }
    }, [isActive]); // isActive 상태가 변경될 때마다 실행됨
    return (
        <header className="bg-black">

            <Container className="container">
                <div className="pc-header d-flex justify-content-between align-items-center py-3">
                    <h1 className="mb-0">
                        <Link to="/"><img className="d-block" width={162} src={logo} alt=""/></Link>
                    </h1>
                    <nav className="d-flex">
                        <ul className="d-flex list-unstyled mb-0 gap-3 ">
                            {link.map((item, index) => (
                                <li key={index}>
                                    <Link to={item.href}
                                          className="text-white text-decoration-none">{item.name}</Link>
                                </li>
                            ))}
                        </ul>
                    </nav>
                    <ul className="d-flex list-unstyled mb-0 gap-3">
                        <li>
                            <Link
                                className="btn bg-transparent border-primary primary color-primary border-btn text-decoration-none"
                                to="/login">로그인</Link>
                        </li>
                        <li>
                            <Link className="btn btn-primary" to="/sign">회원가입</Link>
                        </li>
                    </ul>
                </div>

                <div className="mobile-header">
                    <div className={`offcanvas-backdrop z-2 ${isActive ? "show" : "d-none"}`}></div>

                    <div className="d-flex justify-content-between align-items-center">
                        <h1 className="mb-0 py-3">
                            <Link to="/"><img className="bicLogo d-block" width={180} src={bicLogo} alt=""/></Link>
                        </h1>
                        <div className="toggle-btn d-flex justify-content-center align-items-center flex-column"
                             onClick={handleClick}>
                            <span className="bar"></span>
                            <span className="bar"></span>
                            <span className="bar"></span>
                        </div>
                    </div>
                    <div
                        className={`mo-header px-4 py-4 position-absolute top-0 end-0 z-3 bg-white h-100 ${isActive ? "active" : "d-none "}`}>
                        <div className="d-flex justify-content-between align-items-center">
                            <h2 className="mb-0">
                                <Link to="/"><img className=" d-block" src={logo} alt=""/></Link>
                            </h2>
                            <ul className="d-flex gap-3 list-unstyled mb-0">
                                <li><img src={homeIcon} width={20} alt=""/></li>
                                <li onClick={handleClose} className="cursor"><img src={blackClose} width={18} alt=""/>
                                </li>
                            </ul>
                        </div>
                        <div>
                            <h4 className="fw-bold my-4 mt-5">안정적인 선물거래!<br/><span className="color-primary">테더베이스</span>가 함께 합니다!</h4>
                            <ul className="d-flex list-unstyled mb-0 gap-3">
                                <li className="w-100">
                                    <Link className="w-100 btn btn-primary rounded-4" to="/login">로그인</Link>
                                </li>
                                <li className="w-100">
                                    <Link
                                        className="w-100 btn bg-transparent border-primary primary color-primary border-btn text-decoration-none rounded-4"
                                        to="/sign">회원가입</Link>
                                </li>
                            </ul>
                        </div>
                        <hr className="my-5"/>
                        <nav className="">
                            <ul className="d-flex flex-column list-unstyled mb-0 gap-3 ">
                                {link.map((item, index) => (
                                    <li key={index}>
                                        <Link to={item.href}
                                              className="color-secondary text-decoration-none d-block py-2">
                                            <div className="d-flex align-items-center justify-content-between w-100">
                                                <div className="d-flex align-items-center gap-3">
                                                    <img src={item.icon} width={20} alt=""/>
                                                    <span>{item.name}</span>
                                                </div>
                                                <img src={arrowRightIcon} height={15} alt=""/>
                                            </div>
                                        </Link>
                                    </li>
                                ))}
                            </ul>
                        </nav>

                    </div>
                </div>
            </Container>
        </header>
    );
};

export default Header;
